import React from 'react';

import header from '../images/platziconf-logo.svg'
import './styles/BadgeEdit.css'
import Badge from '../components/Badge'
import BadgeForm from '../components/BadgeForm'
import api from '../api'
import PageLoading from '../components/PageLoading'

class BadgeEdit extends React.Component{
    state = { 
        loading: false, 
        error: null,
        form: {
            firstName: '',
            lastName: '',
            email: '',
            jobTitle: '',
            twitter: '',
            avatarUrl: ' ',
        }
    };

    componentDidMount(){
        this.fetchData()

    }

    fetchData = async (e) => {
        this.setState({loading: true, error: null}) //antes de que se haga la petici칩n, la p치gina se est치 cargando

        try{
            const data = await api.badges.read(this.props.match.params.badgeId) //el m칠todo read toma el id del badge que nos interesa, para extraer ese id usamos el prop de los componentes que usan Route llamado match, accedemos a las variables de la RUTA ESPECIFICADA EN ROUTE mediante el obj params, luego indicamos el obj en cuesti칩n que es badgeId
            this.setState({ loading:false, form: data}) //guardamos los datos en form 
        }catch(error){
            this.setState({loading: false, error: error})
        }
    }

    handleChange = e => {
        this.setState({
            form: { //en este obj form, se ir치n a침adiendo los estados 
                // [e.target.name]: e.target.value no lo implementamos de esta manera porque se sobreescriben los valores
                ...this.state.form, //se desesctructura el objeto para que "caigan" los valores y no se sobreescriban
                [e.target.name]: e.target.value
            }
        })
    }

    handleSubmit = async e => {
        e.preventDefault() //detenemos el submit para que el navegador no lo env칤e a x p치gina
        this.setState({ loading:true, error:null })

        try{
            await api.badges.update(this.props.match.params.badgeId, this.state.form) //hacemos la petici칩n con el m칠todo UPDATE de api, hace una petici칩n PUT, este m칠todo se encarga de actualizar contenidos
            this.setState({ loading: false})

            this.props.history.push('/badges') //utilizamos un m칠todo de los props llamado history, el cual nos servir치 para mandar nuestra p치gina a otra direcci칩n
        }catch (error){
            this.setState({ loading: false, error: error})
        }
    }
    render(){
        if(this.state.loading){
            return <PageLoading />
        }
        return (
            <React.Fragment>                
                <div className="BadgeEdit__hero">
                    <img className="BadgeEdit__hero-image img-fluid" src={header} alt=""/>
                </div>

                <div className="container">
                    <div className="row">
                        <div className="col">
                            <Badge //badge puede disponer f치cilmente de los datos almacenados en el obj form del obj state 
                                firstName={this.state.form.firstName || 'FIRST_NAME'} 
                                lastName={this.state.form.lastName || 'LAST_NAME'} 
                                jobTitle={this.state.form.jobTitle || 'JOB_TITLE'} 
                                twitter={this.state.form.twitter || 'TWITTER'} 
                                email={this.state.form.email}
                                avatarUrl="https://s.gravatar.com/avatar/7fe4dd1c42563678b98d86bb329c316a?s=80"
                            />
                        </div>

                        <div className="col">
                        <h1>Edit</h1>
                            <BadgeForm 
                                onChange={this.handleChange} 
                                onSubmit={this.handleSubmit}
                                formValues={this.state.form}
                                error={this.state.error}
                            />
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}


//BadgeForm tiene como props el evento onChange, el cual lanzar치 su funci칩n handleChange, 
//En el c칩digo de BadgeForm podemos ver que cada que a los inputs se les genere el evento onChange, se direcciona hacia el evento onChange de SUS PROPS
//Por lo cual, cada que haya un cambio en los inputs, se lanzar치 esta funci칩n handleChange
//La cual establece que se hara setState al objeto state, que tiene dentro un obj form. Desestrucutra dicho objeto y se le a침aden los valores: a침ade el nombre del input y su value

//FormValues es igual a lo que contiene el obj form del obj state

//Se elimin칩 Navbar debido a que ahora representa un Layout el cual es contenedor de las dem치s p치ginas

//OPERADOR OR || : Este operador lo podemos utilizar para evaluar 2 o m치s argumentos, si no se cumple el primero, entonces se ejecuta el siguiente

export default BadgeEdit;
