const BASE_URL = 'http://localhost:3001';

const delay = ms => new Promise(resolve => setTimeout(resolve, ms));
const randomNumber = (min = 0, max = 1) =>
  Math.floor(Math.random() * (max - min + 1)) + min;
const simulateNetworkLatency = (min = 30, max = 1500) =>
  delay(randomNumber(min, max));

async function callApi(endpoint, options = {}) {
  await simulateNetworkLatency();

  options.headers = {
    'Content-Type': 'application/json',
    Accept: 'application/json',
  };

  const url = BASE_URL + endpoint;
  const response = await fetch(url, options);
  const data = await response.json();

  return data;
}

const api = {
  badges: {
    list() { //M칄TODO GET, solicita una representaci칩n de un recurso en espec칤fico, las peticiones que usan este m칠todo SOLO RECUPERAN DATOS -- Por ello se utiliza en Badges
      return callApi('/badges'); //callApi es una funci칩n interna para correr un request hacia una API, que en este caso es el SERVER /badges es la direcci칩n de donde se hace la petici칩n, el m칠todo es GET
      //GET hace una petici칩n al servidor de todos los datos que tiene guardados y presenta esos datos en badges
    },
    create(badge) {
      // throw new Error ('500 server Error') //este error se visualizar칤a en badgenew
      return callApi(`/badges`, {
        method: 'POST', //Se utiliza para enviar una entidad a un recurso en espec칤fico, con POST enviamos los datos del nuevo badge hacia /badges
        body: JSON.stringify(badge),//POST es equivalente a realizar un INSERT en la base de datos
      }); //POST envia los datos del badge al servidor, hace un insert en la base de datos, se env칤an hacia la url /badges
    },
    read(badgeId) {
      return callApi(`/badges/${badgeId}`); //este m칠todo, extrae los datos guardados de badges/id 
      //GET hace una petici칩n al servidor de todos los badges (datos) que tiene guardados, y los presenta en la url badges/id del badge, usamos cada id para aumentar la especificidad de los resultados
      //De este modo cada que hacemos clic en un badge, accedemos a 칠l mediante su id en la url
    },
    update(badgeId, updates) { //recibe el badge id que podemos obtener mediante match y los updates
      return callApi(`/badges/${badgeId}`, {
        method: 'PUT', //PUT se encarga de hacer updates, actualiza contenidos
        body: JSON.stringify(updates),
      });
    },
    // Lo hubiera llamado `delete`, pero `delete` es un keyword en JavaScript asi que no es buena idea :P
    remove(badgeId) {
      return callApi(`/badges/${badgeId}`, {
        method: 'DELETE',
      });
    },
  },
};

export default api;
